<template>
  <div class="container mb-5">
    <div class="row">
      <div class="col-md-12">
        <b-button
          variant="success"
          :to="{name: 'reservation-new'}"
        >
          <b-icon-plus-circle></b-icon-plus-circle>
          Crear Reserva
        </b-button>
      </div>
      <div class="col-md-12">
        <b-overlay
          :show="isLoading"
          rounded="lg"
          opacity="0.6"
          spinner-small
          spinner-variant="primary"
        >
          <template #overlay>
            <div class="d-flex align-items-center">
              <b-spinner small type="grow" variant="secondary"></b-spinner>
              <b-spinner type="grow" variant="dark"></b-spinner>
              <b-spinner small type="grow" variant="secondary"></b-spinner>
              <span class="sr-only">Cargando...</span>
            </div>
          </template>
          <b-card
            title="Reservaciones Registradas"
            tag="article"
            class="mb-2 mt-2"
          >
            <b-card-text>
              <b-pagination
                v-model="currentPage"
                :total-rows="rows"
                aria-controls="table-categories"
              ></b-pagination>
              <b-table
                id="table-categories"
                striped
                hover
                :items="reservations"
                :per-page="perPage"
                :current-page="currentPage"
                :fields="fields"
                ref="table"
              >
                <template #cell(options)="row">
                  <b-button
                    variant="info"
                    size="sm"
                    v-b-tooltip.hover
                    title="Detalle Reserva"
                    :to="{
                      name: 'reservation-detail',
                      params: {reservation_id: row.item.id}
                    }"
                  >
                    <b-icon-view-list></b-icon-view-list>
                  </b-button>

                </template>
              </b-table>
            </b-card-text>
          </b-card>
        </b-overlay>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import routeApi from "@/plugins/routesApi";

export default {
  name: "Index",
  data() {
    return {
      reservations: [],
      perPage: 7,
      currentPage: 1,
      rows_room: null,
      fields: [
        {
          key: 'total',
          label: 'Total(Bs.)'
        },
        {
          key: 'check_in',
          label: 'Fecha de Ingreso',
        },
        {
          key: 'check_out',
          label: 'Fecha de Salida'
        },
        {
          key: 'state_reservation',
          label: 'Estado Reserva'
        },
        {
          key: 'options',
          label: 'Opciones'
        }
      ],
      isLoading:false,
    }
  },
  mounted() {
    this.getReservation()
  },
  methods: {
    getReservation(){
      this.isLoading = true
      axios.get(
        routeApi.vue_app_route.api + 'reservation/',
      ).then((res) => {
        if(res.status === 200){
          this.reservations = res.data
        }
        else{
          this.$swal.fire('Algo Salio Mal!', '', 'danger')
        }
      }).finally(() => {
        this.$refs.table.refresh()
        this.isLoading = false
      })
    },

  },
  computed: {
    rows() {
      return this.reservations.length
    }
  }
}
</script>

