<template>
<div class="container">
  <div class="row">
    <div class="col-md-7">
      <b-overlay
        :show="isLoading"
        rounded="lg"
        opacity="0.6"
        spinner-small
        spinner-variant="primary"
      >
        <template #overlay>
          <div class="d-flex align-items-center">
            <b-spinner small type="grow" variant="secondary"></b-spinner>
            <b-spinner type="grow" variant="dark"></b-spinner>
            <b-spinner small type="grow" variant="secondary"></b-spinner>
            <span class="sr-only">Cargando...</span>
          </div>
        </template>
        <b-card
          title="Crear Reservación"
          tag="article"
          class="mb-2 mt-2"
        >
          <b-card-text>
            <validation-observer
              dv-slot="{ invalid }"
              ref="formRegisterRoom"
            >
              <b-form
                @submit.stop.prevent="registerReservation"
              >
                <validation-provider
                  v-slot="{ errors }"
                  name="input-check-in"
                  rules="required|dateToday"
                  :custom-messages="{required: 'Debe Registrar la Fecha de ingreso'}"
                >
                  <b-form-group
                    id="input-check-in"
                    label="Fecha de Ingreso:"
                    label-for="input-check-in"
                  >
                    <b-form-input
                      id="input-check-in"
                      name="input-check-in"
                      type="date"
                      v-model="form.check_in"
                      placeholder="Fecha de Ingreso"

                    />
                    <span
                      class="input-invalid-message text-danger"
                      style="font-size: 13px;"
                    >
                      {{ errors[0] }}
                    </span>
                  </b-form-group>
                </validation-provider>

                <validation-provider
                  v-slot="{ errors }"
                  name="input-check-out"
                  rules="required|dateToday|required|compareDate:@input-check-in"
                  :custom-messages="{
                    required: 'Debe Registrar la fecha de salida',
                  }"
                >
                  <b-form-group
                    id="input-check-out"
                    label="Fecha de Salida:"
                    label-for="input-check-out"
                  >
                    <b-form-input
                      id="input-check-out"
                      name="input-check-out"
                      type="date"
                      v-model="form.check_out"
                      placeholder="Fecha de Salida"
                      onchange=""

                    />
                    <span
                      class="input-invalid-message text-danger"
                      style="font-size: 13px;"
                    >
                      {{ errors[0] }}
                    </span>
                  </b-form-group>
                </validation-provider>

                <b-form-group>
                  <b-button
                    variant="primary"
                    type="submit"
                  >
                    <b-icon-plus-circle></b-icon-plus-circle>
                    Registrar
                  </b-button>
                </b-form-group>
              </b-form>
            </validation-observer>
          </b-card-text>
        </b-card>
      </b-overlay>
    </div>
    <div class="col-md-5">
      <!--div class="row">
        <div class="col-md-12">
          <b-button
            variant="success"
            :to="{name: 'reservation-new'}"
          >
            <b-icon-plus-circle></b-icon-plus-circle>
            Asignar Habitación
          </b-button>
        </div>
        <div class="col-md-12">
          <h3>Detalle de Reserva</h3>
        </div>
        <div class="col-md-12">

        </div>
      </div-->
    </div>
  </div>
</div>
</template>

<script>
import { ValidationObserver, ValidationProvider } from 'vee-validate'
import { extend } from 'vee-validate';
import axios from "axios";
import routeApi from "@/plugins/routesApi";

extend('positive', value => {
  return value >= 0;
});
extend("dateToday", {
  // message: field => "la " + `${field}` + errorMessage,
  message: "La fecha no puede ser una menor a hoy",
  validate: value => {
    let date = new Date()
    let check_date = new Date(value+' 23:59:59')
    if (date >= check_date) {
      return false
    } else {
      return true
    }
  }
});
extend('compareDate', {
  params: ['target'],
  validate(value, { target }) {
    let check_out_date = new Date(value+' 23:59:59')
    let check_in_date = new Date(target+' 23:59:59')
    return check_out_date > check_in_date;
  },
  message: 'La fecha de salida no puede ser menor o igual a la fecha de salida'
});

export default {
  name: "New",
  data() {
    return {
      form: {
        check_in: null,
        check_out: null,
        total: 0,
        state_reservation: 'Pendiente',
      },
      isLoading:false,
    }
  },
  components: {
    ValidationProvider,
    ValidationObserver,
  },
  methods: {
    registerReservation(){
      const Toast = this.$swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 5000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', this.$swal.stopTimer)
          toast.addEventListener('mouseleave', this.$swal.resumeTimer)
        }
      })
      this.$refs.formRegisterRoom.validate().then(success => {
        if (success) {
          this.isLoading = true
          let formData = new FormData()
          formData.append('total', this.form.total)
          formData.append('check_in', this.form.check_in)
          formData.append('check_out', this.form.check_out)
          formData.append('state_reservation', this.form.state_reservation)
          axios.post(
            routeApi.vue_app_route.api + 'reservation/',
            formData, {
              headers: {
                'Content-Type': `"multipart/form-data"; 'boundary'=${formData._boundary}`
              }
            }
          ).then((res) => {
            if (res.status === 201){
              Toast.fire({
                icon: 'success',
                title: 'Reservación Creada registrada correctamente'
              })
              this.$router.push({
                name: 'reservation-detail',
                params: {
                  reservation_id: res.data.id,
                }
              })
            }
            else {
              Toast.fire({
                icon: 'error',
                title: 'Algo salio mal!!!'
              })
            }
          }).finally(() => {
            this.isLoading = false
          })
        }
        else{
          Toast.fire({
            icon: 'danger',
            title: 'Algo salio mal con la información registrada'
          })
        }
      })
    },
    validateCheckin(){

    }
  }
}
</script>

