<template>
  <div class="container mb-5">
    <div class="row">
      <div class="col-md-12">
        <b-button
          variant="success"
          :to="{name: 'room-new'}"
        >
          <b-icon-plus-circle></b-icon-plus-circle>
          Registrar
        </b-button>
      </div>
      <div class="col-md-12">
        <b-overlay
          :show="isLoading"
          rounded="lg"
          opacity="0.6"
          spinner-small
          spinner-variant="primary"
        >
          <template #overlay>
            <div class="d-flex align-items-center">
              <b-spinner small type="grow" variant="secondary"></b-spinner>
              <b-spinner type="grow" variant="dark"></b-spinner>
              <b-spinner small type="grow" variant="secondary"></b-spinner>
              <span class="sr-only">Cargando...</span>
            </div>
          </template>
          <b-card
            title="Habitaciones Registradas"
            tag="article"
            class="mb-2 mt-2"
          >
            <b-card-text>
              <b-pagination
                v-model="currentPage"
                :total-rows="rows"
                aria-controls="table-categories"
              ></b-pagination>
              <b-table
                id="table-categories"
                striped
                hover
                :items="rooms"
                :per-page="perPage"
                :current-page="currentPage"
                :fields="fields"
                ref="table"
              >
                <template #cell(state)="row">
                  <div class="h2 mb-0">
                    <b-icon
                      v-if="row.item.state === true"
                      icon="exclamation-circle-fill"
                      variant="success"
                    ></b-icon>
                    <b-icon
                      v-else
                      icon="exclamation-circle-fill"
                      variant="warning"
                    ></b-icon>
                  </div>
                </template>
                <template #cell(options)="row">
                  <b-button
                    variant="warning"
                    size="sm"
                    v-b-tooltip.hover
                    title="Editar Habitación"
                    :to="{
                      name: 'room-edit',
                      params: {room_id: row.item.id}
                    }"
                  >
                    <b-icon-pencil-square></b-icon-pencil-square>
                  </b-button>
                  <b-button
                    variant="info"
                    size="sm"
                    v-b-tooltip.hover
                    title="Detalle Habitación"
                    :to="{
                      name: 'room-detail',
                      params: {room_id: row.item.id}
                    }"
                  >
                    <b-icon-view-list></b-icon-view-list>
                  </b-button>

                  <b-button
                    variant="danger"
                    size="sm"
                    v-b-tooltip.hover
                    title="Eliminar Habitación"
                    @click="deleteRoom(row.item.id)"
                  >
                    <b-icon-trash></b-icon-trash>
                  </b-button>
                </template>
              </b-table>
            </b-card-text>
          </b-card>
        </b-overlay>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import routeApi from "@/plugins/routesApi";

export default {
  name: "Index",
  data() {
    return {
      rooms: [],
      perPage: 7,
      currentPage: 1,
      rows_room: null,
      fields: [
        {
          key: 'number',
          label: 'Numero Habitación'
        },
        {
          key: 'type',
          label: 'Tipo Habitación',
        },
        {
          key: 'features',
          label: 'Caracteristicas'
        },
        {
          key: 'price',
          label: 'Precio(Bs.)'
        },
        {
          key: 'state',
          label: 'Estado Habitación'
        },
        {
          key: 'options',
          label: 'Opciones'
        }
      ],
      isLoading:false,
    }
  },
  mounted() {
    this.getRoom()
  },
  methods: {
    getRoom(){
      this.isLoading = true
      axios.get(
        routeApi.vue_app_route.api + 'room/',
      ).then((res) => {
        this.rooms = res.data
      }).finally(() => {
        this.$refs.table.refresh()
        this.isLoading = false
      })
    },
    deleteRoom(room_id){
      this.$swal.fire({
        title: 'Seguro que desea eliminar la categoria?',
        showCancelButton: true,
        confirmButtonText: 'No',
        cancelButtonText: 'Eliminar',
      }).then((result) => {
        if (result.isDismissed) {
          this.isLoading
          axios.delete(
            `${routeApi.vue_app_route.api}room/${room_id}/`,
          ).then((res) => {
            if(res.status === 204){
              this.$swal.fire('Categoria Eliminada!', '', 'success')
            }
            else{
              this.$swal.fire('Algo Salio Mal!', '', 'danger')
            }
          }).finally(() => {
            this.getRoom()
            this.isLoading = false
          })
        }
      })
    }
  },
  computed: {
    rows() {
      return this.rooms.length
    }
  }
}
</script>

