<template>
  <div class=container>
    <div class="row">
      <div class="col-md-7">
        <div class="row">
          <div class="col-md-12">
            <h4>Asignar Habitación</h4>
          </div>
          <div class="col-12">
            <p
              class="lead"
            >
              <b>Numero Habitacion:</b> {{this.room.number}}
            </p>
            <p
              class="lead"
            >
              <b>Tipo Habitacion:</b> {{this.room.type}}
            </p>
            <p
              class="lead"
            >
              <b>Precio Habitacion: </b> {{this.room.price}} Bs.
            </p>
          </div>
          <div class="col-md-12">
            <b-overlay
              :show="isLoading"
              rounded="lg"
              opacity="0.6"
              spinner-small
              spinner-variant="primary"
            >
              <template #overlay>
                <div class="d-flex align-items-center">
                  <b-spinner small type="grow" variant="secondary"></b-spinner>
                  <b-spinner type="grow" variant="dark"></b-spinner>
                  <b-spinner small type="grow" variant="secondary"></b-spinner>
                  <span class="sr-only">Cargando...</span>
                </div>
              </template>
              <b-card
                tag="article"
                class="mb-2 mt-2"
              >
                <b-card-text>
                  <validation-observer
                    dv-slot="{ invalid }"
                    ref="formRegisterAddRoom"
                  >
                    <b-form
                      @submit.stop.prevent="registerAddRoom"
                    >
                      <validation-provider
                        v-slot="{ errors }"
                        name="input-customer-name"
                        rules="required"
                        :custom-messages="{required: 'Debe registrar el nombre del cliente'}"
                      >
                        <b-form-group
                          id="input-customer-name"
                          label="Nombre Cliente:"
                          label-for="input-customer-name"
                        >
                          <b-form-input
                            id="input-customer-name"
                            name="input-customer-name"
                            v-model="form.customer_name"
                            placeholder="Nombre Cliente"

                          />
                          <span
                            class="input-invalid-message text-danger"
                            style="font-size: 13px;"
                          >
                            {{ errors[0] }}
                          </span>
                        </b-form-group>
                      </validation-provider>

                      <validation-provider
                        v-slot="{ errors }"
                        name="input-customer-address"
                        rules="required"
                        :custom-messages="{required: 'Debe registrar la direccion del cliente'}"
                      >
                        <b-form-group
                          id="input-customer-address"
                          label="Direccion Cliente:"
                          label-for="input-customer-address"
                        >
                          <b-form-input
                            id="input-customer-address"
                            name="input-customer-address"
                            v-model="form.customer_address"
                            placeholder="Direccion Cliente"

                          />
                          <span
                            class="input-invalid-message text-danger"
                            style="font-size: 13px;"
                          >
                            {{ errors[0] }}
                          </span>
                        </b-form-group>
                      </validation-provider>

                      <validation-provider
                        v-slot="{ errors }"
                        name="input-customer-phone"
                        rules="required"
                        :custom-messages="{required: 'Debe registrar el telefono del cliente'}"
                      >
                        <b-form-group
                          id="input-customer-phone"
                          label="Telefono Cliente:"
                          label-for="input-customer-phone"
                        >
                          <b-form-input
                            id="input-customer-phone"
                            name="input-customer-phone"
                            v-model="form.customer_phone"
                            placeholder="Telefono Cliente"

                          />
                          <span
                            class="input-invalid-message text-danger"
                            style="font-size: 13px;"
                          >
                            {{ errors[0] }}
                          </span>
                        </b-form-group>
                      </validation-provider>

                      <validation-provider
                        v-slot="{ errors }"
                        name="input-customer-genre"
                        rules="required"
                        :custom-messages="{
                          required: 'Debe seleccionar el genero',
                        }"
                      >
                        <b-form-group
                          id="input-customer-genre"
                          label="Genero cliente:"
                          label-for="input-customer-genre"
                        >
                          <b-form-select
                            v-model="form.customer_genre"
                            :options="customer_genre"
                          ></b-form-select>
                          <span
                            class="input-invalid-message text-danger"
                            style="font-size: 13px;"
                          >
                            {{ errors[0] }}
                          </span>
                        </b-form-group>
                      </validation-provider>

                      <b-form-group>
                        <b-button
                          variant="primary"
                          type="submit"
                        >
                          <b-icon-plus-circle></b-icon-plus-circle>
                          Registrar
                        </b-button>
                      </b-form-group>
                    </b-form>
                  </validation-observer>
                </b-card-text>
              </b-card>
            </b-overlay>
          </div>
        </div>
      </div>
      <div class="col-md-5"></div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import routeApi from "@/plugins/routesApi";

export default {
  name: "AddRoom",
  data() {
    return {
      reservation: [],
      reservation_id: this.$route.params.reservation_id,
      room_id: this.$route.params.room_id,
      isLoading:false,
      room: [],
      form: {
        customer_name: null,
        customer_phone: null,
        customer_address: null,
        customer_genre: null,
      },
      customer_genre: [
        { value: null, text: 'Seleccione una opción' },
        { value: 'Femenino', text: 'Femenino' },
        { value: 'Masculino', text: 'Masculino' },
      ]
    }
  },
  mounted() {
    this.getRoom()
  },
  methods: {
    getRoom(){
      this.isLoading = true
      axios.get(
        `${routeApi.vue_app_route.api}room/${this.room_id}/`,
      ).then((res) => {
        if(res.status === 200){
          this.room = res.data
        }
        else {
          console.log('Algo salio mal!!!')
        }
      }).finally(() => {
        this.isLoading = false
      })
    },
    registerAddRoom(){
      const Toast = this.$swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 5000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', this.$swal.stopTimer)
          toast.addEventListener('mouseleave', this.$swal.resumeTimer)
        }
      })
      this.$refs.formRegisterAddRoom.validate().then(success => {
        if (success) {
          this.isLoading = true
          let formData = new FormData()
          formData.append('customer_name', this.form.customer_name)
          formData.append('customer_phone', this.form.customer_phone)
          formData.append('customer_genre', this.form.customer_genre)
          formData.append('customer_address', this.form.customer_address)
          formData.append('reservation_id', this.reservation_id)
          formData.append('room_id', this.room_id)
          axios.post(
            routeApi.vue_app_route.api + 'detail-reservation/',
            formData, {
              headers: {
                'Content-Type': `"multipart/form-data"; 'boundary'=${formData._boundary}`
              }
            }
          ).then((res) => {
            if (res.status === 200){
              Toast.fire({
                icon: 'success',
                title: 'Habitación asignada correctamente'
              })
              this.$router.push({
                name: 'reservation-detail',
                params: {
                  reservation_id: this.reservation_id,
                }
              })
            }
          }).finally(() => {
            this.isLoading = false
          })
        }
        else{
          Toast.fire({
            icon: 'danger',
            title: 'Algo salio mal con la información registrada'
          })
        }
      })
    }
  }
}
</script>

