<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <h4>Datos de Facturación</h4>
      </div>
      <div class="col-md-12">
        <div class="col-md-12">
          <b-overlay
            :show="isLoading"
            rounded="lg"
            opacity="0.6"
            spinner-small
            spinner-variant="primary"
          >
            <template #overlay>
              <div class="d-flex align-items-center">
                <b-spinner small type="grow" variant="secondary"></b-spinner>
                <b-spinner type="grow" variant="dark"></b-spinner>
                <b-spinner small type="grow" variant="secondary"></b-spinner>
                <span class="sr-only">Cargando...</span>
              </div>
            </template>
            <b-card
              tag="article"
              class="mb-2 mt-2"
            >
              <b-card-text>
                <validation-observer
                  dv-slot="{ invalid }"
                  ref="formRegisterPay"
                >
                  <b-form
                    @submit.stop.prevent="registerPay"
                  >
                    <validation-provider
                      v-slot="{ errors }"
                      name="input-business-name"
                      rules="required"
                      :custom-messages="{required: 'Debe registrar la razon social'}"
                    >
                      <b-form-group
                        id="input-business-name"
                        label="Razon Social:"
                        label-for="input-business-name"
                      >
                        <b-form-input
                          id="input-customer-name"
                          name="input-business-name"
                          v-model="form.business_name"
                          placeholder="Razon Social"

                        />
                        <span
                          class="input-invalid-message text-danger"
                          style="font-size: 13px;"
                        >
                            {{ errors[0] }}
                          </span>
                      </b-form-group>
                    </validation-provider>

                    <validation-provider
                      v-slot="{ errors }"
                      name="input-nit"
                      rules="required"
                      :custom-messages="{required: 'Debe registrar el nit'}"
                    >
                      <b-form-group
                        id="input-nit"
                        label="Direccion Cliente:"
                        label-for="input-nit"
                      >
                        <b-form-input
                          id="input-nit"
                          name="input-nit"
                          v-model="form.nit"
                          placeholder="NIT"

                        />
                        <span
                          class="input-invalid-message text-danger"
                          style="font-size: 13px;"
                        >
                            {{ errors[0] }}
                          </span>
                      </b-form-group>
                    </validation-provider>

                    <validation-provider
                      v-slot="{ errors }"
                      name="input-type"
                      rules="required"
                      :custom-messages="{
                        required: 'Debe seleccionar el tipo de habitación',
                      }"
                    >
                      <b-form-group
                        id="input-type"
                        label="Tipo de Habitación:"
                        label-for="input-type"
                      >
                        <b-form-select
                          v-model="form.payment_method"
                          :options="type_payment"
                        ></b-form-select>
                        <span
                          class="input-invalid-message text-danger"
                          style="font-size: 13px;"
                        >
                    {{ errors[0] }}
                  </span>
                      </b-form-group>
                    </validation-provider>

                    <b-form-group>
                      <b-button
                        variant="primary"
                        type="submit"
                      >
                        <b-icon-plus-circle></b-icon-plus-circle>
                        Registrar Pago
                      </b-button>
                    </b-form-group>
                  </b-form>
                </validation-observer>
              </b-card-text>
            </b-card>
          </b-overlay>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import routeApi from "@/plugins/routesApi";

export default {
  name: "PayReservation",
  data() {
    return {
      reservation: [],
      isLoading:false,
      room: [],
      form: {
        business_name: null,
        nit: null,
        total: null,
        payment_method: 'Efectivo',
      },
      type_payment: [
        { value: null, text: 'Seleccione una opción' },
        { value: 'Efectivo', text: 'Efectivo' },
        { value: 'Tarjeta', text: 'Tarjeta' },
      ]
    }
  },
  props: {
    reservation_id: Number,
    invoice: Array,
  },
  methods: {
    registerPay(){
      const Toast = this.$swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 5000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', this.$swal.stopTimer)
          toast.addEventListener('mouseleave', this.$swal.resumeTimer)
        }
      })
      this.$refs.formRegisterPay.validate().then(success => {
        if (success) {
          this.isLoading = true
          let formData = new FormData()
          formData.append('business_name', this.form.business_name)
          formData.append('nit', this.form.nit)
          formData.append('payment_method', this.form.payment_method)
          formData.append('reservation_id', this.reservation_id)
          axios.post(
            routeApi.vue_app_route.api + 'invoice/',
            formData, {
              headers: {
                'Content-Type': `"multipart/form-data"; 'boundary'=${formData._boundary}`
              }
            }
          ).then((res) => {
            console.log(res)
            if (res.status === 200){
              Toast.fire({
                icon: 'success',
                title: 'Habitación registrada correctamente'
              })
              this.invoice = res.data
            }
            else {
              console.log('Algo salio mal')
            }
          }).finally(() => {
            this.isLoading = false
          })
        }
        else{
          Toast.fire({
            icon: 'danger',
            title: 'Algo salio mal con la información registrada'
          })
        }
      })
    }
  }
}
</script>

