<template>
<div class="container">
  <div class="col-md-7">
    <b-overlay
      :show="isLoading"
      rounded="lg"
      opacity="0.6"
      spinner-small
      spinner-variant="primary"
    >
      <template #overlay>
        <div class="d-flex align-items-center">
          <b-spinner small type="grow" variant="secondary"></b-spinner>
          <b-spinner type="grow" variant="dark"></b-spinner>
          <b-spinner small type="grow" variant="secondary"></b-spinner>
          <span class="sr-only">Cargando...</span>
        </div>
      </template>
      <b-card
        title="Registrar Habitación"
        tag="article"
        class="mb-2 mt-2"
      >
        <b-card-text>
          <validation-observer
            dv-slot="{ invalid }"
            ref="formRegisterRoom"
          >
            <b-form
              @submit.stop.prevent="registerRoom"
            >
              <validation-provider
                v-slot="{ errors }"
                name="input-number"
                rules="required"
                :custom-messages="{required: 'Debe registrar el numero de habitación'}"
              >
                <b-form-group
                  id="input-number"
                  label="Numero de habitación:"
                  label-for="input-number"
                >
                  <b-form-input
                    id="input-number"
                    name="input-number"
                    v-model="form.number"
                    placeholder="Numero de habitación"

                  />
                  <span
                    class="input-invalid-message text-danger"
                    style="font-size: 13px;"
                  >
                    {{ errors[0] }}
                  </span>
                </b-form-group>
              </validation-provider>

              <validation-provider
                v-slot="{ errors }"
                name="input-type"
                rules="required"
                :custom-messages="{
                  required: 'Debe seleccionar el tipo de habitación',
                }"
              >
                <b-form-group
                  id="input-type"
                  label="Tipo de Habitación:"
                  label-for="input-type"
                >
                  <b-form-select
                    v-model="form.type"
                    :options="type_room"
                  ></b-form-select>
                  <span
                    class="input-invalid-message text-danger"
                    style="font-size: 13px;"
                  >
                    {{ errors[0] }}
                  </span>
                </b-form-group>
              </validation-provider>

              <validation-provider
                v-slot="{ errors }"
                name="input-features"
                rules="required"
                :custom-messages="{
                  required: 'Debe registrar las caracteristicas de la habitación',
                }"
              >
                <b-form-group
                  id="input-features"
                  label="Caracteristicas:"
                  label-for="input-features"
                >
                  <b-form-textarea
                    id="input-features"
                    name="input-features"
                    v-model="form.features"
                    placeholder="Caracteristicas"
                  />
                  <span
                    class="input-invalid-message text-danger"
                    style="font-size: 13px;"
                  >
                    {{ errors[0] }}
                  </span>
                </b-form-group>
              </validation-provider>

              <validation-provider
                v-slot="{ errors }"
                name="input-price"
                rules="required"
                :custom-messages="{
                  required: 'Debe registrar el precio de la habitación',
                }"
              >
                <b-form-group
                  id="input-price"
                  label="Precio (Bs.):"
                  label-for="input-price"
                >
                  <b-form-input
                    id="input-price"
                    name="input-price"
                    type="number"
                    v-model="form.price"
                    placeholder="Precio Habitación (Bs)"
                  />
                  <span
                    class="input-invalid-message text-danger"
                    style="font-size: 13px;"
                  >
                    {{ errors[0] }}
                  </span>
                </b-form-group>
              </validation-provider>

              <b-form-checkbox
                v-model="form.state"
                name="check-leaf" switch
              >
                Estado Habitación
              </b-form-checkbox>

              <b-form-group>
                <b-button
                  variant="primary"
                  type="submit"
                >
                  <b-icon-plus-circle></b-icon-plus-circle>
                  Registrar
                </b-button>
              </b-form-group>
            </b-form>
          </validation-observer>
        </b-card-text>
      </b-card>
    </b-overlay>
  </div>
  <div class="col-md-5"></div>
</div>
</template>

<script>
import { ValidationObserver, ValidationProvider } from 'vee-validate'
import { extend } from 'vee-validate';
import axios from "axios";
import routeApi from "@/plugins/routesApi";

extend('positive', value => {
  return value >= 0;
});

export default {
  name: "New",
  data() {
    return {
      form: {
        number: null,
        type: null,
        features: null,
        price: 0,
        state: false,
      },
      isLoading:false,
      type_room: [
        { value: null, text: 'Seleccione una opción' },
        { value: 'Simple', text: 'Simple' },
        { value: 'Doble', text: 'Doble' },
        { value: 'Triple', text: 'Triple' },
        { value: 'Matrimonial', text: 'Matrimonial' },
      ]
    }
  },
  components: {
    ValidationProvider,
    ValidationObserver,
  },
  methods: {
    registerRoom(){
      const Toast = this.$swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 5000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', this.$swal.stopTimer)
          toast.addEventListener('mouseleave', this.$swal.resumeTimer)
        }
      })
      this.$refs.formRegisterRoom.validate().then(success => {
        if (success) {
          this.isLoading = true
          let formData = new FormData()
          formData.append('number', this.form.number)
          formData.append('type', this.form.type)
          formData.append('features', this.form.features)
          formData.append('price', this.form.price)
          formData.append('state', this.form.state)
          axios.post(
            routeApi.vue_app_route.api + 'room/',
            formData, {
              headers: {
                'Content-Type': `"multipart/form-data"; 'boundary'=${formData._boundary}`
              }
            }
          ).then((res) => {
            if (res.status === 201){
              Toast.fire({
                icon: 'success',
                title: 'Habitación registrada correctamente'
              })
            }
          }).finally(() => {
            this.isLoading = false
          })
        }
        else{
          Toast.fire({
            icon: 'danger',
            title: 'Algo salio mal con la información registrada'
          })
        }
      })
    }
  }
}
</script>

