<template>
  <div class="container">
    <b-overlay
      :show="isLoading"
      rounded="lg"
      opacity="0.6"
      spinner-small
      spinner-variant="primary"
    >
      <template #overlay>
        <div class="d-flex align-items-center">
          <b-spinner small type="grow" variant="secondary"></b-spinner>
          <b-spinner type="grow" variant="dark"></b-spinner>
          <b-spinner small type="grow" variant="secondary"></b-spinner>
          <span class="sr-only">Cargando...</span>
        </div>
      </template>
        <div class="row">
          <div class="col-md-12">
            <b-card
              title="Registrar Categoria"
              tag="article"
              class="mb-2 mt-2"
            >
              <b-card-text>
                <p
                  class="lead"
                >
                  <b>Categoria: </b>{{category.name}}
                </p>
                <p
                  class="lead"
                >
                  <b>Level: </b>{{category.level}}
                </p>

                <p
                  class="lead"
                >
                  <b>Mejor Oferta: </b>
                  <template v-if="category.best_offer">SI</template>
                  <template v-else>NO</template>
                </p>

                <p
                  class="lead"
                >
                  <b>Subcategoria: </b>
                  <template v-if="category.leaf">SI</template>
                  <template v-else>NO</template>
                </p>

                <p
                  class="lead"
                >
                  <b>Pago Automático: </b>
                  <template v-if="category.auto_pay">SI</template>
                  <template v-else>NO</template>
                </p>
              </b-card-text>
              <b-button
                :to="{
                  name: 'room-edit',
                  params: {category_id: category_id}
                }"
                variant="warning"
              >
                <b-icon-pencil-square></b-icon-pencil-square>
                Modificar
              </b-button>
              <b-button
                variant="danger"
                v-b-tooltip.hover
                title="Eliminar Categoria"
                @click="deleteCategory(category_id)"
              >
                <b-icon-trash></b-icon-trash>
                Eliminar
              </b-button>
            </b-card>
          </div>
        </div>
    </b-overlay>
  </div>
</template>

<script>
import axios from "axios";
import routeApi from "@/plugins/routesApi";

export default {
  name: "Detail",
  data() {
    return {
      category: [],
      category_id: this.$route.params.category_id,
      isLoading:false
    }
  },
  mounted() {
    this.getCategory()
  },
  methods:{
    getCategory(){
      this.isLoading = true
      axios.get(
        `${routeApi.vue_app_route.api}category/${this.category_id}/`,
      ).then((res) => {
        this.category.name = res.data.category_name
        this.category.auto_pay = res.data.auto_pay_enabled
        this.category.best_offer = res.data.best_offer_enabled
        this.category.level = res.data.category_level
        this.category.leaf = res.data.leaf_category
        this.category.category_parent_id = res.data.category_parent_id
      }).finally(() => {
        this.isLoading = false
      })
    },
    deleteCategory(category_id){
      this.$swal.fire({
        title: 'Seguro que desea eliminar la categoria?',
        showCancelButton: true,
        confirmButtonText: 'No',
        cancelButtonText: 'Eliminar',
      }).then((result) => {
        if (result.isDismissed) {
          this.isLoading
          axios.delete(
            `${routeApi.vue_app_route.api}category/${category_id}/`,
          ).finally(() => {
            this.$swal.fire('Categoria Eliminada!', '', 'success')
            this.$router.push({
              name: 'room-index',
            })
            this.isLoading = false
          })
        }
      })
    }
  }
}
</script>

