<template>
  <div class="container-fluid">
    <b-overlay
      :show="isLoading"
      rounded="lg"
      opacity="0.6"
      spinner-small
      spinner-variant="primary"
    >
      <template #overlay>
        <div class="d-flex align-items-center">
          <b-spinner small type="grow" variant="secondary"></b-spinner>
          <b-spinner type="grow" variant="dark"></b-spinner>
          <b-spinner small type="grow" variant="secondary"></b-spinner>
          <span class="sr-only">Cargando...</span>
        </div>
      </template>
        <div class="row">
          <div class="col-md-5">
            <b-card
              title="Detalle Reservación"
              tag="article"
              class="mb-2 mt-2"
            >
              <b-card-text>
                <p
                  class="lead"
                >
                  <b>Fecha de Ingreso: </b>{{reservation.check_in}}
                </p>
                <p
                  class="lead"
                >
                  <b>Fecha de Salida: </b>{{reservation.check_out}}
                </p>
                <p
                  class="lead"
                >
                  <b>Estado Reservación: </b>{{reservation.state_reservation}}
                </p>
                <h5>
                  <b>Costo Total: </b>{{reservation.total}} Bs.
                </h5>
              </b-card-text>

              <b-card-text
                v-if="!this.invoice.length"
              >
                <b-button
                  v-b-modal.modal-pay
                  variant="success"
                >
                  <b-icon-plus-circle></b-icon-plus-circle>
                  Pagar
                </b-button>
              </b-card-text>
            </b-card>
            <b-card>
              <h4>Detalles de Facturación</h4>
              <template
                v-for="inv in invoice"
              >
                <p
                  :key="inv.id"
                  class="lead"
                >
                  <b>Razon Social:</b> {{inv.business_name}}
                </p>

                <p
                  :key="inv.business_name"
                  class="lead"
                >
                  <b>Nit:</b> {{inv.nit}}
                </p>

                <p
                  :key="inv.nit"
                  class="lead"
                >
                  <b>Tipo de Pago:</b> {{inv.payment_method}}
                </p>
              </template>
            </b-card>
          </div>
          <div class="col-md-7">
            <b-card>
              <div class="row">
                <div class="col-md-12">
                  <h4>Detalle Habitaciones</h4>
                </div>
                <div class="col-md-12">
                  <b-button
                    v-b-modal.modal-1
                    variant="success"
                  >
                    <b-icon-plus-circle></b-icon-plus-circle>
                    Agregar Habitación
                  </b-button>
                </div>
                <div class="col-md-12">
                  <b-modal
                    id="modal-1"
                    title="Habitaciones"
                    size="xl"
                  >
                    <free-rooms :reservation_id="reservation_id"></free-rooms>
                  </b-modal>
                </div>
                <div class="col-md-12">
                  <b-overlay
                    :show="isLoading"
                    rounded="lg"
                    opacity="0.6"
                    spinner-small
                    spinner-variant="primary"
                  >
                    <template #overlay>
                      <div class="d-flex align-items-center">
                        <b-spinner small type="grow" variant="secondary"></b-spinner>
                        <b-spinner type="grow" variant="dark"></b-spinner>
                        <b-spinner small type="grow" variant="secondary"></b-spinner>
                        <span class="sr-only">Cargando...</span>
                      </div>
                    </template>
                    <b-card
                      title="Habitaciones Libres"
                      tag="article"
                      class="mb-2 mt-2"
                    >
                      <b-card-text>
                        <b-pagination
                          v-model="currentPage"
                          :total-rows="rows"
                          aria-controls="table-categories"
                        ></b-pagination>
                        <b-table
                          id="table-categories"
                          striped
                          hover
                          :items="rooms"
                          :per-page="perPage"
                          :current-page="currentPage"
                          :fields="fields"
                          ref="table"
                        >
                          <template #cell(options)="row">
                            <b-button
                              variant="danger"
                              size="sm"
                              v-b-tooltip.hover
                              title="pencel"
                            >
                              <b-icon-trash :class="row"></b-icon-trash>
                            </b-button>
                          </template>
                        </b-table>
                      </b-card-text>
                    </b-card>
                  </b-overlay>
                </div>
              </div>
            </b-card>
          </div>
        </div>
    </b-overlay>
    <b-modal
      id="modal-pay"
      title="Pagar"
      size="xl"
    >
      <pay-reservation :reservation_id="reservation_id" :invoice="invoice"></pay-reservation>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";
import routeApi from "@/plugins/routesApi";
import FreeRooms from "@/views/reservation/FreeRooms";
import PayReservation from "@/views/reservation/PayReservation";

export default {
  name: "Detail",
  data() {
    return {
      reservation: [],
      reservation_id: this.$route.params.reservation_id,
      rooms: [],
      invoice: [],
      perPage: 7,
      currentPage: 1,
      isLoading:false,

      rows_room: null,
      fields: [
        {
          key: 'room_number',
          label: 'Numero Habitación'
        },
        {
          key: 'room_type',
          label: 'Tipo Habitación',
        },
        {
          key: 'room_features',
          label: 'Caracteristicas'
        },
        {
          key: 'room_price',
          label: 'Precio(Bs.)'
        },
        {
          key: 'options',
          label: 'Opciones'
        }
      ],
    }
  },
  components: {
    FreeRooms,
    PayReservation,
  },
  mounted() {
    this.getReservation()
    this.getRooms()
    this.getInvoice()
  },
  methods:{
    getInvoice(){
      this.isLoading = true
      axios.get(
        `${routeApi.vue_app_route.api}invoice/reservation/${this.reservation_id}`,
      ).then((res) => {
        console.log(res)
        if(res.status === 200){
          this.invoice = res.data
        }
        else {
          console.log('Algo Salio mal !!!!')
        }
      }).finally(() => {
        this.isLoading = false
      })
    },
    getReservation(){
      this.isLoading = true
      axios.get(
        `${routeApi.vue_app_route.api}reservation/${this.reservation_id}/`,
      ).then((res) => {
        if(res.status === 200){
          this.reservation.check_in = res.data.check_in
          this.reservation.check_out = res.data.check_out
          this.reservation.total = res.data.total
          this.reservation.state_reservation = res.data.state_reservation
        }
        else {
          console.log('Algo Salio mal !!!!')
        }
      }).finally(() => {
        this.isLoading = false
      })
    },
    getRooms(){
      this.isLoading = true
      axios.get(
        `${routeApi.vue_app_route.api}detail-reservation/rooms/${this.reservation_id}`,
      ).then((res) => {
        if(res.status === 200){
          this.rooms = res.data
        }
        else {
          console.log('Algo Salio mal !!!!')
        }
      }).finally(() => {
        this.isLoading = false
      })
    },
    payReservation(reservation_id){
      this.$swal.fire({
        title: 'Seguro que desea eliminar la categoria?',
        showCancelButton: true,
        confirmButtonText: 'No',
        cancelButtonText: 'Eliminar',
      }).then((result) => {
        if (result.isDismissed) {
          this.isLoading
          axios.delete(
            `${routeApi.vue_app_route.api}category/${reservation_id}/`,
          ).finally(() => {
            this.$swal.fire('Categoria Eliminada!', '', 'success')
            this.$router.push({
              name: 'room-index',
            })
            this.isLoading = false
          })
        }
      })
    }
  },
  computed: {
    rows() {
      return this.rooms.length
    }
  }
}
</script>

