import Vue from 'vue'
import Router from 'vue-router'

import AppFooter from "@/components/AppFooter";
import AppHeader from "@/components/AppHeader";

// Routes
// ROOMS
import IndexRoom from "@/views/room/Index"
import NewRoom from "@/views/room/New"
import EditRoom from "@/views/room/Edit"
import DetailRoom from "@/views/room/Detail"

//RESERVATIONS
import IndexReservation from "@/views/reservation/Index"
import NewReservation from "@/views/reservation/New"
import EditReservation from "@/views/reservation/Edit"
import DetailReservation from "@/views/reservation/Detail"
import AddRoom from "@/views/reservation/AddRoom"

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: "/",
      name: "room-index",
      components: {
        header: AppHeader,
        default: IndexRoom,
        footer: AppFooter
      }
    },
    {
      path: "/room/new",
      name: "room-new",
      components: {
        header: AppHeader,
        default: NewRoom,
        footer: AppFooter
      }
    },
    {
      path: "/room/edit/:room_id",
      name: "room-edit",
      components: {
        header: AppHeader,
        default: EditRoom,
        footer: AppFooter
      }
    },
    {
      path: "/room/detail/:room_id",
      name: "room-detail",
      components: {
        header: AppHeader,
        default: DetailRoom,
        footer: AppFooter
      }
    },
    //RESERVATION
    {
      path: "/reservation",
      name: "reservation-index",
      components: {
        header: AppHeader,
        default: IndexReservation,
        footer: AppFooter
      }
    },
    {
      path: "/reservation/new",
      name: "reservation-new",
      components: {
        header: AppHeader,
        default: NewReservation,
        footer: AppFooter
      }
    },
    {
      path: "/room/edit/:reservation_id",
      name: "reservation-edit",
      components: {
        header: AppHeader,
        default: EditReservation,
        footer: AppFooter
      }
    },
    {
      path: "/reservation/detail/:reservation_id",
      name: "reservation-detail",
      components: {
        header: AppHeader,
        default: DetailReservation,
        footer: AppFooter
      }
    },
    {
      path: "/reservation/add/room/:reservation_id/:room_id",
      name: "reservation-add-room",
      components: {
        header: AppHeader,
        default: AddRoom,
        footer: AppFooter
      }
    },
  ],
})
